<template>
  <layout-default>
    <div class="page-content">
        <div class="container-fluid">

            <!-- start page title -->
            <div class="row">
                <div class="col-12">
                    <div class="page-title-box d-flex align-items-center justify-content-between">
                        <h4 class="mb-0">تفاصيل الفاتورة</h4>
                        <form-modal ref="FormModal" :fetchData="fetchData"/>
                    </div>
                </div>
            </div>
            <!-- end page title -->

            <div class="row">
                <div class="col-lg-12" v-if="order">
                    <div class="card">
                        <div class="card-body" style="margin: 20px;">
                            <div class="invoice-title">
                                <h4 class="float-end font-size-16" style="text-align:center;">رقم الفاتورة #{{order.id}}{{order.created_at| moment("DDMMYY")}}
                                <br>
                                <br>
                                تاريخ الفاتورة {{order.created_at| moment("DD-MM-YYYY")}}
                                </h4>
                                <div class="mb-4">
                                    
                                </div>
                                <div class="text-muted" v-if="order.supplier">
                                    <p class="mb-1">
                                        <b style="float: left;"> أسم المورد : </b>&nbsp;{{order.supplier.name}} 
                                    </p>
                                </div>
                                <div class="text-muted" v-else style="height: 50px;">
                                    <p class="mb-1">
                                        <b style="float: left;"> نوع الفاتورة : </b>&nbsp;Admin Order 
                                    </p>
                                </div>
                            </div>

                            <hr class="my-4">

                            <div class="py-2">
                                <h5 class="font-size-15">تفاصيل الفاتورة</h5>

                                <div class="table-responsive" v-for="(order_product, index) in order.order_products" style="overflow-x: clip;">
                                    <table class="table table-nowrap table-centered mb-0">
                                        <thead v-if="index==0">
                                            <tr>
                                                <th style="width: 70px;">No.</th>
                                                <th>المنتج</th>
                                                <th>السعر</th>
                                                <th>الكمية</th>
                                                <th>المتاح</th>
                                                <th>إجمالي</th>
                                                <th >خيارات</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <th scope="row">{{index+1}}</th>
                                                <td class="col-md-3">
                                                    <h5 class="font-size-15 mb-1">{{order_product.product.name}}</h5>
                                                </td>
                                                <td >{{moneyFormat(order_product.price,0)}}</td>
                                                <td >{{order_product.qty}}</td>
                                                <td >{{order_product.avaliable}}</td>
                                                <td >{{moneyFormat(order_product.price*order_product.qty,0)}}</td>
                                                <td>
                                                    <div class="btn-group">
                                                        <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                        المزيد <i class="mdi mdi-chevron-down"></i>
                                                        </button>
                                                        <div class="dropdown-menu" style="min-width: 120px;">
                                                            <!--
                                                                
                                                            <router-link :to="{ name: 'supplier_orders_show', params: { order_id: order.id } }" tag="a" class="dropdown-item">
                                                                <i class="far fa-eye"></i> رؤية
                                                            </router-link>
                                                            -->
                                                            <a @click="$refs.FormModal.edit(order_product)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                <i class="far fa-edit"></i> تعديل
                                                            </a>
                                                            <a v-if="order_product.editable" @click="$refs.FormModal.destroy(order.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                <i class="far fa-trash-alt"></i> حذف
                                                            </a>
                                                        </div>
                                                    </div>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    <div class="row">
                                        <hr style="color: #888;">
                                        <div class="row ms-5">
                                            <div class="col-md-3 col-sm-4 col-xs-6 font-size-12" v-for="detail in order_product.details">
                                                <label v-if="detail.option_value"><b>{{detail.option.name}}</b> / {{detail.option_value.name}}</label> 
                                                <label v-if="detail.value"><b>{{detail.option.name}}</b> / {{detail.value}}</label> 
                                            </div>
                                        </div>
                                        <hr style="color: #888;">
                                    </div>
                                </div>
                              
                                
                                <div class="row">
                                    <h4 style="text-align: right;"><b>إجمالي الفاتورة :</b> {{moneyFormat(order.total,0)}}</h4>
                                </div>
                                <div class="d-print-none mt-4">
                                    <div class="float-end">
                                        <a href="javascript:window.print()" class="btn btn-success waves-effect waves-light me-1"><i class="fa fa-print"></i></a>
                                        <a href="javascript:;" @click="$refs.FormModal.destroy(order.id)" class="btn btn-danger w-md waves-effect waves-light">حذف</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end row -->
            
        </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/supplier/order';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
        return {
            order:null
        }
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    fetchData() {
      axios.get('/api/supplier_orders/'+this.$route.params.order_id).then(({data}) => {
          this.order=data.success.order;
      }).catch((error) => {this.$router.push({ name: "supplier_orders"})});  
    },
  },
}
</script>